    background-color: #fff;
    color: rgba(0,0,0,.87);
    >.mu-tab-link {
      color: rgba(102,102,102,1);
    }
    >.mu-tab-active{
      color: @primaryColor;
    }
  }
  .activeTab{
    padding:10px;
  }
  .mu-text-field{
    width: 100%;
  }
  .mu-text-field.mu-select-field{
    width: 50%;
  }
</style>
